
.ac_main{
	position: relative;
	width: 1240px;
	margin: 92px auto 0 auto;
}

body{
	font-family: "\5FAE\8F6F\96C5\9ED1";
}
a:hover{
	cursor: pointer;
	color:#fd4c5d;
}
#aaa input{
	font-size: 14px;
	color: #999;
	padding: 8px 10px;
	border: 1px solid #e5e5e5;
	border-right:none ;
	width: 316px;
	height: 32px;
	line-height: 32px;
	border-radius: 3px 0 0 3px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.header{
	height:92px;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
.header-top{
	height: 46px;
	width: 100%;
	font-size: 12px;
}
.header-sec{
	height: 92px;
	width: 100%;
	font-size: 12px;
}
.wp{
	width: 1240px;
	margin:0 auto;
}
.one{
	height: 46px;
	float: left;
}
.li{
	float: left;
}

.main_right{
	position: absolute;
	left: 0;
	right: 360px;
	margin-top: 30px;
}
.main_right .second .lili{
	float: left;
	height: 50px;
	margin-bottom: 20px;
	width: 200px;
}
.main_right .second .lili a{
		float: left;
		display: block;
}
.main_right .second .lili a img{
		display: block;
		height: 50px;
		width: 50px;
}
.main_right .second .lili .name{
		line-height: 50px;
		float: left;
		height: 50px;
		width: 140px;
		font-size: 13px;
		margin-left: 9px;
}
.for_bot a{
		color: #979797;
}
.for_bot a:hover{
		color: #red;
}

.a-column-right{
	float: right;
	width: 290px;
	margin-top: 30px;
}
.recom-item{
	float: left;
	width: 130px;
	margin-right:20px;
	margin-bottom: 20px;
}
.recom-item:nth-child(2n+0){
	margin-right: 0px;
}
